.sectionTitle
{
    font-family: Aesthetic;
    font-size: 100px;

    span 
    {
        position: relative;
        left: -150px;
        opacity: 0;
        animation: appear 650ms ease-in-out forwards;
    }
}

@keyframes appear {
    from 
    {
        left: -150px;
        opacity: 0;
    }
    to 
    {
        left: 0;
        opacity: 1;
    }
}