@font-face {
    font-family: Aesthetic;
    src: url("./Font/Aesthetic-Regular.woff2");
}

:root
{
    --BACKGROUND: #1a2b30;
    --TEXT: #2c7b91;
}

body
{
    background-color: var(--BACKGROUND);
    color: var(--TEXT);
    margin: 0;
    width: 100vw;
    overflow-x: hidden;
    overflow-y: scroll;
}

section
{
    max-width: min(90vw, 1200px);
    margin: auto;
}